import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { CustomValidators } from 'ng2-validation';
import { AddCategoryService } from '../../services/AddCategoryService';
import { GetPopularChemistService } from '../../services/GetPopularChemistService';
import { GetChemistDataService } from '../../services/GetChemistDataService';

declare var jQuery: any;

@Component({
  selector: 'add-category',
  templateUrl: './add-category.component.html',
  styleUrls: [
    './add-category.component.scss',
    '../../../../assets/icon/icofont/css/icofont.scss'
  ],
  providers: [AddCategoryService, GetPopularChemistService, GetChemistDataService]
})
export class AddCategoryComponent implements OnInit {

  public category_name: string = "";
  public sub_category_name: string = "";
  public chemists_array = [];
  public chemist_id: number = 0;
  public sub_chemist_id: number = 0;
  public category_id: number = 0;
  public chemists_categories = [];

  public category_runner: boolean = false;
  public sub_category_runner: boolean = false;
  public showAttr:boolean = false;
  constructor(public _addCategoryService: AddCategoryService,
    public _getChemistDataService: GetChemistDataService,
    public _getPopularChemistService: GetPopularChemistService) {
    var session_obj = JSON.parse(localStorage.getItem('session_obj'));
      if(session_obj.UserRole == 1){
        this.getChemists();
        this.showAttr = true;
      }
      else if(session_obj.UserRole == 2){
        this.chemist_id = session_obj.ChemistId;
        this.sub_chemist_id = session_obj.ChemistId;
        this.showAttr = false;
        this.getCategoriesOfChemists();
      }
  }
  addCategory() {
    if (this.category_runner == false) {
      this.category_runner = true;
      $('.text-danger').hide();
      this.chemist_id = parseFloat(this.chemist_id.toString());
      if (this.chemist_id <= 0) {
        $('#pharmacist_id_error').show();
        this.category_runner = false;
        return;
      }
      if (this.category_name === undefined || this.category_name == "" || this.category_name == null) {
        $('#category-name-error').show();
        this.category_runner = false;
        return;
      }
      this._addCategoryService.addCategory(this.chemist_id, this.category_name, 0).subscribe(
        response => {
          if (response.code == 200) {
            $('#categories-add').show();
            setTimeout(function () {
              $('#categories-add').hide();
            }, 5000);
            console.log(response);
            this.chemists_categories.push(response.data);
            this.category_name = "";
            this.chemist_id = 0;
            this.category_runner = false;
          }
        }
      );
    }
  }//End of Add Category
  addSubCategory() {
    if (this.sub_category_runner == false) {
      this.sub_category_runner = true;
      $('.text-danger').hide();
      this.sub_chemist_id = parseFloat(this.sub_chemist_id.toString());
      this.category_id = parseFloat(this.category_id.toString());
      if (this.sub_chemist_id <= 0) {
        $('#sub_pharmacist_id_error').show();
        this.sub_category_runner = false;
        return;
      }
      if (this.category_id <= 0) {
        $('#category_id_error').show();
        this.sub_category_runner = false;
        return;
      }
      if (this.sub_category_name === undefined || this.sub_category_name == "" || this.sub_category_name == null) {
        $('#category-name-error').show();
        this.sub_category_runner = false;
        return;
      }
      this._addCategoryService.addSubcategory(this.sub_chemist_id, this.sub_category_name, this.category_id).subscribe(
        response => {
          if (response.code == 200) {
            $('#sub-categories-add').show();
            setTimeout(function () {
              $('#sub-categories-add').hide();
            }, 5000);
            this.sub_category_name = "";
            this.chemist_id = 0;
            this.sub_category_runner = false;
          }
        }
      );
    }
  }// End of Add Sub Category
  getChemists() {
    this._getChemistDataService.getRegisteredChemistService().subscribe(
      response => {
        // this.chemists_array = response.data;

        for (var i = 0; i < response.data.length; i++) {
          this.chemists_array.push(response.data[i].Chemist);
        }
      });
  }// End of Get Chemists
  getCategoriesOfChemists() {
    $('#categories-fetcher').show();
    this._addCategoryService.getCateogry(this.sub_chemist_id).subscribe(
      response => {
        $('#categories-fetcher').hide();
        this.chemists_categories = response.data;
      });
  }// End of Get Categories of Chemists
  ngOnInit(): void {
  }
}



